body {
  /* align-items: center; */
  display: flex;
  justify-content: center;
  height: 100vh;
}

h1 {
  color: #6933ff !important;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  text-align: center;
}

.chest-util {
  max-width: 400px;
  /* text-align: center; */
  border-radius: 5px;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 20px;
  box-shadow: 0px 0px 5px black;
}

code {
  background-color: #eee;
  border: 1px solid #999;
  display: block;
  padding: 20px;
}

.submission-boxes {
  text-align: center;
}

.try-it {
  text-align: right;
}

input[type="submit"] {
  border: none;
  background-color: #6933ff;
  border-radius: 100px;
  color: white;
  font-size: large;
  margin-top: 5px;
  padding: 7px;
  width: 100px;
}

button {
  border: none;
  background-color: #6933ff;
  border-radius: 100px;
  color: white;
  font-size: large;
  margin-top: 5px;
  padding: 7px;
  width: 100px;
}

p {
  text-align: left;
}

.example {
  margin-bottom: 10px;
}
